import API from "../api";


export default class AuthService {
    static login(payload) {
        return API.ax.post('auth/login', payload).catch(e => console.log(e))
    }



    static createToken(data){
        return API.ax.post('auth/create/security/token', data).catch(e => console.log(e))
    }


    static getUser(){
        return API.ax.get(`auth/activate?token=${sessionStorage.getItem('token')}`).catch(e => console.log(e))
    }


    static getRegistrationInfo() {
        return API.ax.get(`registration/read/info?individualId=${sessionStorage.getItem('refId')}`).catch(e => console.log(e))
    }

    static getAcademicResultsInfo() {
        return API.ax.get(`registration/read/education/level?individualId=${sessionStorage.getItem('refId')}`).catch(e => console.log(e))
    }





}