import React, { useState } from "react";
import {
  Grid,
  CircularProgress,
  Typography,
  Button,
  Tabs,
  Tab,
  TextField,
  Fade,
} from "@material-ui/core";
import { withRouter } from "react-router-dom";
import classnames from "classnames";
import AuthService from "../../services/auth/auth_service";

// styles
import useStyles from "./styles";

// logo
import logo from "../../images/logo.png";
import google from "../../images/google.svg";

// context
import { useUserDispatch, loginUser } from "../../context/UserContext";

function Login(props) {
  var classes = useStyles();

  // global
  var userDispatch = useUserDispatch();

  // local
  var [isLoading, setIsLoading] = useState(false);
  var [error, setError] = useState(null);
  var [activeTabId, setActiveTabId] = useState(0);
  var [registrationNumber, setRegistrationNumber] = useState("");
  var [loginValue, setLoginValue] = useState("");
  var [passwordValue, setPasswordValue] = useState("");
  var [repeatPasswordValue, setRepeatPasswordValue] = useState("");


  var [passwordError, setPasswordError] = useState("");
  var [emailError, setEmailError] = useState("");
  var [repeatPasswordError, setRepeatPasswordError] = useState("");
  var [passwordLoginError, setPasswordLoginError] = useState("");

  const handleCreateAccount = () => {
    setPasswordError("");
    setRepeatPasswordError("");

    if (passwordValue !== repeatPasswordValue) {
      setRepeatPasswordError("Passwords do not match");
      return;
    } else if (passwordValue.length < 8) {
      setPasswordError("Password must be at least 8 characters");
      return;
    } else if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(loginValue)) {
      setEmailError("Enter valid email");
      return;
    }

    const data = {
      'username': registrationNumber,
      'email': loginValue,
      password: passwordValue,
      'regType': 'provision'
    };

    AuthService.createToken(data).then(res => {
      console.log(res)
     
          if (res.status === 200) {
            sessionStorage.setItem('token', res.data.token)
            alert("Validation link sent to your email, please check your email.")
            setActiveTabId(0)

          //   AuthService.getUser().then(res => {
          //     if (res.status === 200) {
          //      console.log(res.data)
          //   }
          // })


            // window.location.reload()

            // loginUser(
            //   userDispatch,
            //   loginValue,
            //   passwordValue,
            //   props.history,
            //   setIsLoading,
            //   setError,
            // )

            // userDispatch({
            //   type: "LOGIN",
            //   payload: res.data
            // });
            // props.history.push("/app/dashboard");
          }
     
    })


     console.log(data)
   };



   const handleLogin = () => {

    if (!loginValue || !passwordValue) {
      return;
    }else if (passwordLoginError.length < 8) {
      setPasswordLoginError("Password must be at least 8 characters");
      return;
    } 

    const data = {
      'username': loginValue,
      'password': passwordValue
    }


    AuthService.login(data).then(res => {
      console.log(res)
      
      
        sessionStorage.setItem('token', res.data.token)
        sessionStorage.setItem('refId', res.data.refId)
        sessionStorage.setItem('fullname', res.data.fullname)
        sessionStorage.setItem('category', res.data.category)
        sessionStorage.setItem('regType', res.data.regType)
        // window.location.reload()

        loginUser(
          userDispatch,
          loginValue,
          passwordValue,
          props.history,
          setIsLoading,
          setError,
        )
     
    }).catch(e => {
      console.log(e)
    })

    // loginUser(
    //   userDispatch,
    //   loginValue,
    //   passwordValue,
    //   props.history,
    //   setIsLoading,
    //   setError,
    // );
  };



  const handleKeyPress = (e) => {
    if (e.key === "Enter") {
      handleLogin();
    }
  };



  return (
    <Grid container className={classes.container}>
      <div className={classes.logotypeContainer} style={{ backgroundColor: '#cba135' }}>
        <img src={logo} alt="logo" className={classes.logotypeImage} />
        <Typography className={classes.logotypeText}>Integrity and Fairness</Typography>
      </div>
      <div className={classes.formContainer}>
        <div className={classes.form}>
          <Tabs
            value={activeTabId}
            onChange={(e, id) => setActiveTabId(id)}
            indicatorColor="primary"
            textColor="primary"
            centered
          >
            <Tab label="Login" classes={{ root: classes.tab }} />
            <Tab label="New User" classes={{ root: classes.tab }} />
          </Tabs>
          {activeTabId === 0 && (
            <React.Fragment>
              {/* <Typography variant="h1" className={classes.greeting}>
                Good Morning,
              </Typography>
               */}
              <Fade in={error}>
                <Typography color="secondary" className={classes.errorMessage}>
                  Something is wrong with your login or password :(
                </Typography>
              </Fade>
              <TextField
                id="email"
                InputProps={{
                  classes: {
                    underline: classes.textFieldUnderline,
                    input: classes.textField,
                  },
                }}
                value={loginValue}
                onChange={e => setLoginValue(e.target.value)}
                margin="normal"
                placeholder="Registration Number"
                type="text"
                fullWidth
              />
              <TextField
                id="password"
                InputProps={{
                  classes: {
                    underline: classes.textFieldUnderline,
                    input: classes.textField,
                  },
                }}
                value={passwordValue}
                onChange={e => setPasswordValue(e.target.value)}
                margin="normal"
                placeholder="Password"
                type="password"
                onKeyPress={handleKeyPress}
                fullWidth
              />
              <div className={classes.formButtons}>
                {isLoading ? (
                  <CircularProgress size={26} className={classes.loginLoader} />
                ) : (
                  <Button color="primary" style={{

                    fontWeight: 'bold',
                    ...(passwordValue.length === 0 ||
                      passwordValue.length === 0

                      ? { opacity: 0.5 } // Apply this style when button is disabled
                      : { backgroundColor: '#cba135', }) // Empty object when button is enabled
                  }}
                    disabled={
                      loginValue.length === 0 || passwordValue.length === 0
                    }
                    onClick={() =>
                      handleLogin()
                    }
                    variant="contained"
                    // color="primary"
                    size="large"
                    error={Boolean(passwordLoginError)}
                    helperText={passwordLoginError}
                  >
                    Login
                  </Button>
                )}
                <Button
                  color="primary"
                  size="large"
                  className={classes.forgetButton}
                >
                  Forget Password
                </Button>
              </div>
            </React.Fragment>
          )}
          {activeTabId === 1 && (
            <React.Fragment>
              <Typography variant="h1" className={classes.greeting}>
                Welcome!
              </Typography>
              {/* <Typography variant="h2" className={classes.subGreeting}>
                Create your account
              </Typography> */}
              <Fade in={error}>
                <Typography color="secondary" className={classes.errorMessage}>
                  Something is wrong with your login or password :(
                </Typography>
              </Fade>
              
              <TextField
                id="email"
                InputProps={{
                  classes: {
                    underline: classes.textFieldUnderline,
                    input: classes.textField,
                  },
                }}
                value={loginValue}
                onChange={e => setLoginValue(e.target.value)}
                margin="normal"
                placeholder="Email Adress"
                type="email"
                fullWidth
                error={Boolean(emailError)}
                helperText={emailError}
              />

              <TextField
                id="name"
                InputProps={{
                  classes: {
                    underline: classes.textFieldUnderline,
                    input: classes.textField,
                  },
                }}
                value={registrationNumber}
                onChange={e => setRegistrationNumber(e.target.value)}
                margin="normal"
                placeholder="Registraion Number"
                type="text"
                fullWidth
              />

              <TextField
                id="password"
                InputProps={{
                  classes: {
                    underline: classes.textFieldUnderline,
                    input: classes.textField,
                  },
                }}
                value={passwordValue}
                onChange={e => setPasswordValue(e.target.value)}
                margin="normal"
                placeholder="Password"
                type="password"
                fullWidth
                error={Boolean(passwordError)}
                helperText={passwordError}
              />

              <TextField
                id="repeat-password"
                InputProps={{
                  classes: {
                    underline: classes.textFieldUnderline,
                    input: classes.textField,
                  },
                }}
                value={repeatPasswordValue}
                onChange={(e) => setRepeatPasswordValue(e.target.value)}
                margin="normal"
                placeholder="Repeat Password"
                type="password"
                fullWidth
                error={Boolean(repeatPasswordError)}
                helperText={repeatPasswordError}
              />
              <div className={classes.creatingButtonContainer}>
                {isLoading ? (
                  <CircularProgress size={26} />
                ) : (
                  <Button
                    // onClick={() =>
                    //   loginUser(
                    //     userDispatch,
                    //     loginValue,
                    //     passwordValue,
                    //     props.history,
                    //     setIsLoading,
                    //     setError,
                    //   )
                    // }
                    onClick={handleCreateAccount}
                    disabled={
                      loginValue.length === 0 ||
                      passwordValue.length === 0 ||
                      registrationNumber.length === 0 ||
                      repeatPasswordValue.length === 0
                    }
                    size="large"
                    variant="contained"
                    color="primary"
                    fullWidth
                    style={{

                      fontWeight: 'bold',
                      ...(loginValue.length === 0 ||
                        passwordValue.length === 0 ||
                        registrationNumber.length === 0 ||
                        repeatPasswordValue.length === 0
                       
                        ? { opacity: 0.5 } // Apply this style when button is disabled
                        : { backgroundColor: '#cba135', }) // Empty object when button is enabled
                    }}
                    className={classes.createAccountButton}
                  >
                    Create your account
                  </Button>
                )}
              </div>
              {/* <div className={classes.formDividerContainer}>
                <div className={classes.formDivider} />
                <Typography className={classes.formDividerWord}>or</Typography>
                <div className={classes.formDivider} />
              </div>
              <Button
                size="large"
                className={classnames(
                  classes.googleButton,
                  classes.googleButtonCreating,
                )}
              >
                <img src={google} alt="google" className={classes.googleIcon} />
                &nbsp;Sign in with Google
              </Button> */}
            </React.Fragment>
          )}
        </div>
        <Typography color="primary" className={classes.copyright}>
           {new Date().getFullYear()} <a style={{ textDecoration: 'none', color: 'inherit' }} href="https://flatlogic.com" rel="noopener noreferrer" target="_blank">Vrb</a>. All rights reserved.
        </Typography>
      </div>
    </Grid>
  );
}

export default withRouter(Login);
